<!-- 景区客流统计柱状图 -->
<template>
  <div :id="domId"
       style="width:100%;height:100%"></div>
</template>

<script>
export default {
  props: ['domId', 'title', 'option'],
  data () {
    //这里存放数据
    return {
      myChart: null,
      myOption: {}
    };
  },
  watch: {
    option (newValue) { //n为新值,o为旧handler (newValue, oldValue) {
      this.myOption = newValue
      this.myChart.setOption(this.myOption)
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.myOption = this.option
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.init()
  },
  //生命周期 - 更新之后
  updated () {
  },

  //方法集合
  methods: {
    init () {
      this.myChart = this.$echarts.init(document.getElementById(this.domId));

      // 指定图表的配置项和数据
      var option = {
        title: {
          text: this.title,
          left: '5%',
          top: '10',
          textStyle: {
            fontSize: 17,
          },
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: 4, // 默认10%，给24就挺合适的。
          top: 80, // 默认60
          right: 14, // 默认10%
          bottom: 10, // 默认60
          containLabel: true
        },
        legend: {
          itemHeight: 12, // 设置高度
          show: true,
          top: 35,
          right: 10,
          textStyle: {
            fontSize: 12,
            color: "#333333",
          },
        },
        xAxis: {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {  // 设置轴标签
            // rotate: -40,
            fontSize: 10,
          },
        },
        yAxis: {
          type: 'value',
          minInterval: 1
        },
        series: [{
          type: 'line',
          // barCategoryGap: '50%',
          // barWidth: '21',
          itemStyle: {
            color: '#5870F8'
          },
          showBackground: true,
          areaStyle: {},
          backgroundStyle: {
            color: 'rgba(220, 220, 220, 0.8)'
          }
        }, {
          type: 'line',
          itemStyle: {
            color: '#16F699'
          },
          showBackground: true,
          areaStyle: {},
          backgroundStyle: {
            color: 'rgba(220, 220, 220, 0.8)'
          }
        }]
      };
      this.myChart.setOption(option)
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
</style>