<!-- 报表统计--景区客流统计 -->
<template>
  <van-row class="dt-home">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">景区客流统计</p>
      </van-col>
    </van-row>
    <van-row class="dt-content">
      <van-row class="dt-top">
        <div class="dt-left-box1">
          <div class="dt-text"><span>今日检票数</span></div>
          <div class="dt-number-left"><span>{{intoNum}}</span></div>
        </div>
        <div class="dt-line"></div>
        <div class="dt-right-box2">
          <div class="dt-text"><span>今日售票数</span></div>
          <div class="dt-number-right"><span>{{ticketNum}}</span></div>
        </div>
      </van-row>

      <div class="dt-chart-box">
        <trAfficAnalysis domId="trA1"
                         title="景区人流实时分析(今日)"
                         :option="option1"></trAfficAnalysis>
      </div>
      <div class="dt-chart-box">
        <trAfficAnalysis domId="trA2"
                         title="周景区人流统计(近七日)"
                         :option="option2"></trAfficAnalysis>
      </div>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
/** 引入组件 */
import trAfficAnalysis from "@/components/echarts/trafficAnalysis";
// import trAfficStatistics from "@/components/echarts/trafficStatistics";

/** 引入接口 */
import {
  queryPeopleFlow,
  // checkPeople,
  peopleTrend,
  ticketNumber,
  // 闸机出入人数
  gateNum,
} from "@/api/chart";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    trAfficAnalysis,
    // trAfficStatistics
  },
  data () {
    //这里存放数据
    return {
      // 今日检票数
      intoNum: 0,
      // 今日售票数
      ticketNum: 0,
      option1: {},
      option2: {}
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    // 获取闸机人流 
    this.getGateNum()
    // 大屏售票数
    this.getTicketNumber()
    // 小时客流人数和人数预测
    this.getPeopleTrend()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () {
  }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    gd (day, mode) {
      let dateTime = new Date()
      dateTime = dateTime.setDate(dateTime.getDate() - day);
      dateTime = new Date(dateTime);
      if (mode == 1) {
        return dateTime.format("MM月dd")
      } else if (mode == 2) {
        return dateTime.format("yyyy-MM-dd 00:00:00")
      }
    },
    // 小时客流人数和人数预测
    getPeopleTrend () {
      peopleTrend().then(res => {
        let dataList = res.data

        let xData = []
        let myData = []
        let prediction = []
        dataList.forEach((item, index) => {
          if (index !== dataList.length - 1) {
            myData.push(item.realTimePeople)
          }
          xData.push(item.createTime)
          prediction.push(item.predictionPeople)
        });

        this.option1 = {
          legend: {
            data: ["当前在园人数", "当前在园预测人数"]
          },
          xAxis: {
            data: xData
          },
          series: [{
            data: myData,
            name: '当前在园人数'
          }, {
            data: prediction,
            name: '当前在园预测人数'
          }]
        }
      })
    },

    // 获取闸机人流
    getGateNum () {
      gateNum().then(res => {
        let data = res.data
        let weekData = data.slice(data.length - 7, data.length)
        let xData = []
        let intoList = []
        let outList = []
        weekData.forEach((item, index) => {
          if (index == weekData.length - 1) {
            this.intoNum = item.inPeople
          }
          xData.push(item.createTime)
          intoList.push(item.inPeople)
          outList.push(item.outPeople)
        });

        this.option2 = {
          legend: {
            data: ["入口", "出口"]
          },
          xAxis: {
            data: xData
          },
          series: [{
            data: intoList,
            name: '入口',
            itemStyle: {
              color: '#FFFD66'
            },
          }, {
            data: outList,
            name: '出口',
            itemStyle: {
              color: '#16F699'
            },
          }]
        }
      })
    },
    // 大屏售票数
    getTicketNumber () {
      ticketNumber().then(res => {
        let data = res.data
        this.ticketNum = data[data.length - 1].number
      })
    },

    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },

    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
// $noticeBarHeight: px2rem(50);
.dt-home {
  height: 100%;
  .dt-title {
    width: 100%;
    height: $headerHeight;
    padding-top: $statusBarHeight;
    background-color: #5870f8;
  }
  .dt-back {
    height: px2rem(14);
    width: px2rem(14);
    margin-left: px2rem(6);
    color: #fff;
    font-size: px2rem(18);
  }
  .dt-home-shouye {
    font-size: px2rem(17);
    font-family: 'SourceHanSansCN-Medium';
    color: #ecedf2;
  }
  .dt-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: calc(100vh - #{$headerHeight} - #{$statusBarHeight});
    background-color: #edf1f5;
    overflow-y: auto;
    .dt-top {
      position: relative;
      width: 95%;
      height: px2rem(110);
      margin-top: px2rem(10);
      background: #ffffff;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
      border-radius: 8px;
      border-radius: 8px;
      .dt-left-box1 {
        float: left;
        left: 0;
        top: 0;
        width: 49%;
        height: px2rem(100);
      }
      .dt-right-box2 {
        float: right;
        right: 0;
        top: 0;
        width: 50%;
        height: px2rem(100);
      }
      .dt-line {
        position: absolute;
        top: px2rem(20);
        left: 50%;
        width: 1px;
        height: px2rem(65);
        background-color: #ebebeb;
        border-radius: 2px;
      }
      .dt-text {
        margin-top: px2rem(25);
        font-size: px2rem(14);
        color: #4e4e4e;
      }
      .dt-number-left {
        margin-top: px2rem(10);
        font-size: px2rem(30);
        font-weight: 500;
        color: #9c6be3;
      }
      .dt-number-right {
        margin-top: px2rem(10);
        font-size: px2rem(30);
        font-weight: 500;
        color: #03c693;
      }
    }
    .dt-chart-box {
      width: 95%;
      height: px2rem(250);
      margin-top: px2rem(10);
      background: #ffffff;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
      border-radius: 8px;
      border-radius: 8px;
    }
    .dt-chart-box-last {
      margin-bottom: px2rem(10);
    }
  }
}
</style>